import logo from './logo.svg';
import './App.css';
import React , {Component}from 'react';

class App extends Component {
  constructor(props){
    super(props);
    this.emailEl=React.createRef();
    
  }

  state={
    text:""
  }

  sub=()=>{
    this.setState({text:"Thank you. you are all set"})
  }

  submitHandler=event =>{
    event.preventDefault();
    const email=this.emailEl.current.value;
    

    if(email.trim().length===0){
        return;
    }
    const requestBody={
        email:email
    };
    fetch(`http://localhost:8080/home/email`,{
            method:'POST',
            body:JSON.stringify(requestBody),
            headers:{
                'Content-Type':'application/json'
            }
        }).then(res =>{
            if (res.status!==200 && res.status!==201){
                throw new Error('Failed');
            }
            return res.json();
        })
        .then(resData =>{
            console.log(resData.userid);
        })
        .catch(err =>{
            console.log(err)
        })
  }
  render(){
    const {text}=this.state
    return (
      <React.Fragment>
        <form className="auth-form" onSubmit={this.submitHandler}>
                        <div className="form-control">

                            <label htmlFor="email">E-Mail.Please after you entered your email, press submit and close the window</label>
                            <input type="email" id="email" ref={this.emailEl}/>
                        </div>
                        <div>
                          {text}
                        </div>
                        <div className="form-actions">
                      
                            <button type="submit" onChange={this.sub}>Submit</button>
                
                        </div>
                       </form>
      </React.Fragment>
    );
  }
}

export default App;
